import React from 'react';
import { Modal, Spin } from '@sinohealth/butterfly-ui-antd';
import { Document, Page, pdfjs } from 'react-pdf';

pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`;

type PdfPreviewProps = {
  visible: boolean;
  pageNumber?: number;
  file?: string;
  width?: number;
  renderMode?: string;
  cancel?: () => void;
};

/**
 * 处方pdf 预览
 * @param param0
 * @returns
 */
const PdfPreview: React.FC<PdfPreviewProps> = ({
  visible,
  pageNumber,
  file = 'https://test.sdc.sinohealth.com/fat/api/oss/preview/d00e15d83fd588212b94d3c3041ac805',
  width = 600,
  renderMode = 'canvas',
  cancel,
}) => {
  const onDocumentLoadSuccess = () => {};

  return (
    <Modal
      visible={visible}
      style={{ top: 5 }}
      maskClosable
      footer={null}
      onCancel={() => {
        cancel && cancel();
      }}
      modalRender={() => (
        <div>
          <Document
            file={file}
            loading={<Spin size="small" />}
            renderMode={renderMode}
            onLoadSuccess={onDocumentLoadSuccess}
          >
            <Page pageNumber={pageNumber} width={width} loading={<Spin size="large" />} />
          </Document>
        </div>
      )}
    />
  );
};

export default PdfPreview;
