/* eslint-disable react/jsx-indent */
import React from 'react';
import { Tooltip } from '@sinohealth/butterfly-ui-antd';
import { CaretDownOutlined, ManOutlined, WomanOutlined } from '@ant-design/icons';
import { getAgeByBirth } from '@/utils/utils';
import styles from './index.less';

type UserInfoProps = {
  clas: any;
  patient?: AdminOrder.PatientInfo;
  delivery?: AdminOrder.DeliveryInfo;
};

const UserInfo: React.FC<UserInfoProps> = (props) => {
  const { clas, patient, delivery } = props;

  const topTip = `${delivery && delivery?.toName}，${delivery && delivery?.toPhone}，${
    delivery && delivery?.toAddress
  }`;

  return (
    <div className={`${styles['user-container']} ${clas}`}>
      <div className={styles['user-avator']}>{patient?.name?.slice(0, 1)}</div>
      <ul className={styles['user-info']}>
        <li className={styles['user-item']}>
          <div className={styles['user-name']}>{patient?.name}</div>
          <div className={styles['user-age']}>
            {patient?.gender === 'male' ? (
              <ManOutlined style={{ color: '#B1C1F7' }} />
            ) : (
              <WomanOutlined style={{ color: '#FFCED9' }} />
            )}
            &nbsp;
            {getAgeByBirth(patient?.birthDate)}
            <span>岁</span>
          </div>
        </li>
        <li className={styles['user-desc']}>
          <div className={styles['desc-label']}>联系电话</div>
          <div className={styles['desc-value']}>{patient?.phone}</div>
        </li>
        <li className={styles['user-desc']}>
          <div className={styles['desc-label']}>身份证号</div>
          <div className={styles['desc-value']}>{patient?.idNumber}</div>
        </li>
        <Tooltip
          placement="top"
          title={
            patient?.allergyHistory !== null
              ? patient?.allergyHistory?.map((el) => (
                  <span key={`${el?.allergen}-${el?.patientId}`}>{el?.allergen}；</span>
                ))
              : '--'
          }
        >
          <li className={styles['user-desc']}>
            <div className={styles['desc-label']}>过敏史</div>
            <div className={`${styles['desc-value']} ${styles.allergen}`}>
              {patient?.allergyHistory !== null
                ? patient?.allergyHistory?.map((el) => (
                    <span key={`${el?.allergen}-${el?.patientId}`}>{el?.allergen}；</span>
                  ))
                : '--'}
            </div>
          </li>
        </Tooltip>
        <Tooltip placement="top" title={delivery ? topTip : '暂无收货信息'}>
          <li className={styles['user-desc']}>
            <div className={styles['desc-label']}>收货人信息</div>
            {delivery ? (
              <div className={styles['desc-value']}>
                <span>{delivery && delivery?.toName}</span>
                <span>，</span>
                <span>{delivery && delivery?.toPhone}</span>
                <span>，</span>
                <span className={styles['to-address']}>{delivery && delivery?.toAddress}</span>
              </div>
            ) : null}
          </li>
        </Tooltip>
      </ul>
      <div className={styles['user-more']}>{/* <CaretDownOutlined /> */}</div>
    </div>
  );
};

export default UserInfo;
