import React from 'react';
import styles from './index.less';

type PayInfoProps = {
  clas: any;
  payInfo?: AdminOrder.OrderInfo | any;
};

/**
 * 支付信息
 * @returns
 */
const PayInfo: React.FC<PayInfoProps> = (props) => {
  const { clas, payInfo } = props;

  // eslint-disable-next-line no-unsafe-optional-chaining
  const total = (payInfo?.totalAmount - payInfo?.deliveryAmount).toFixed(2);

  return (
    <div className={`${styles['pay-container']} ${clas}`}>
      <div className={styles['order-info']}>
        <h4 className={styles['order-title']}>支付信息</h4>
        <ul className={styles.container}>
          <li className={styles['info-item']}>
            <span className={styles['item-label']}>运费：</span>
            <span className={styles['item-value']}>¥{payInfo?.deliveryAmount}</span>
          </li>
          <li className={styles['info-item']}>
            <span className={styles['item-label']}>处方金额：</span>
            <span className={styles['item-value']}>¥{total}</span>
          </li>
          <li className={styles['info-item']}>
            <span className={styles['item-label']}>扣减金额：</span>
            <span className={styles['item-value']}>
              {/* <span className={styles['item-tip']}>8.8折</span> */}
              <span>- ¥{payInfo?.discountAmount}</span>
            </span>
          </li>
        </ul>
      </div>
      <div className={styles['pay-info']}>
        <div className={styles['pay-status']}>已支付</div>
        <div className={styles['pay-title']}>实收金额：</div>
        <div className={styles['pay-price']}>¥{payInfo?.realAmount}</div>
      </div>
    </div>
  );
};

export default PayInfo;
