import { Button, Space } from '@sinohealth/butterfly-ui-antd';
import React from 'react';
import styles from './index.less';

type ControlToolProps = {
  formType?: string; // orderSearch：订单查询 revieOrder：审方订单
  type?: string; // o2o b2c
  status?: string; // 订单状态
  position?: 'static' | 'relative' | 'absolute' | 'sticky' | 'fixed';
  width?: string;
  left?: string;
  cancelOrder?: () => void; // 取消订单
  cancelDelivery?: () => void; // 取消配送
  delivery?: () => void; // 开始配送
  rePrescribe?: () => void; // 重新开方
  reDelivery?: () => void; // 重新配送
  reject?: () => void; // 驳回
  passNow?: () => void; // 审核通过，开始配送
  passWait?: () => void; // 审核通过，稍后配送
  exception?: () => void; // 模拟配送异常
  completion?: () => void; // 模拟完成订单
};

// 取消订单按钮
const CANCEL_ORDER_STATUS: any = {
  wait: true,
  timeout: true,
  begin_delivery: true,
  delivery_exception: true,
};

/**
 * 底部操作栏
 * @returns
 */
const ControlTool: React.FC<ControlToolProps> = (props) => {
  const {
    formType,
    type,
    status,
    position = 'fixed',
    width,
    left,
    cancelOrder,
    cancelDelivery,
    delivery,
    rePrescribe,
    reDelivery,
    reject,
    passNow,
    passWait,
    exception,
    completion,
    children,
  } = props;

  return (
    <div className={styles['control-tool']} style={{ position, width, left }}>
      <span />
      <Space>
        {status && CANCEL_ORDER_STATUS[status] && formType === 'orderSearch' ? (
          <Button type="info" onClick={() => cancelOrder && cancelOrder()}>
            取消订单
          </Button>
        ) : null}
        {status === 'delivery_wait' && formType === 'revieOrder' ? (
          <>
            <Button type="info" onClick={() => cancelDelivery && cancelDelivery()}>
              取消配送
            </Button>
            <Button type="primary" onClick={() => delivery && delivery()}>
              开始配送
            </Button>
          </>
        ) : null}
        {status === 'begin_delivery' && formType === 'revieOrder' ? (
          <>
            <Button type="primary" onClick={() => exception && exception()} danger>
              模拟配送异常
            </Button>
            <Button type="primary" onClick={() => completion && completion()}>
              模拟完成订单
            </Button>
            <Button type="info" onClick={() => cancelDelivery && cancelDelivery()}>
              取消配送
            </Button>
          </>
        ) : null}
        {status === 'delivery_exception' && formType === 'revieOrder' ? (
          <Button type="info" onClick={() => rePrescribe && rePrescribe()}>
            重新开方
          </Button>
        ) : null}
        {status === 'delivery_exception' && formType === 'revieOrder' ? (
          <Button type="primary" onClick={() => reDelivery && reDelivery()}>
            重新配送
          </Button>
        ) : null}
        {status === 'wait' && formType === 'revieOrder' ? (
          <Button type="info" onClick={() => reject && reject()}>
            驳回处方
          </Button>
        ) : null}
        {status === 'wait' && formType === 'revieOrder' ? (
          <Button type="primary" ghost onClick={() => passNow && passNow()}>
            审核通过，开始配送
          </Button>
        ) : null}
        {status === 'wait' && formType === 'revieOrder' && type === 'B2C' ? (
          <Button type="primary" onClick={() => passWait && passWait()}>
            审核通过，稍后配送
          </Button>
        ) : null}
        {/* 自定义传组件 */}
        {children}
      </Space>
    </div>
  );
};

export default ControlTool;
