/* eslint-disable indent */
/* eslint-disable react/jsx-indent */
import React, { useEffect, useState } from 'react';
import { Button, Popover, Tooltip } from '@sinohealth/butterfly-ui-antd/lib';
import {
  CaretDownOutlined,
  CaretUpOutlined,
  InfoCircleFilled,
  InfoCircleOutlined,
  SwapOutlined,
} from '@ant-design/icons';
import styles from './index.less';

const CLOSE_LIST: any = {
  delivering: true,
  delivery_exception: true,
  finished: true,
};

const TAB_LIST: any = [
  { label: '相互作用', value: 0, flid: 'interaction' },
  { label: '用药禁忌', value: 1, flid: 'avoid' },
  { label: '重复用药', value: 2, flid: 'repeatMedication' },
  { label: '超适应症', value: 3, flid: 'beyondIndication' },
];

type RationalAdviceProps = {
  clas: any;
  currentStatus?: string;
  interaction?: any; // 相互作用
  interactionTip?: any; // 相互作用提示
  avoid?: any; // 使用禁忌
  avoidTip?: any; // 药品紧急提示
  beyondIndication?: any; // 超适应症
  repeatMedication?: any; // 重复用药
  repeatMedicationTip?: any; // 重复用药提示
  remarkCode?: string; // AA-有数据显示，BB-完全没有数据显示（药方没问题），CC-部分SKU可查到，另外通过SKU查不到，但可通过“通用名+厂家”查到，,CC-部分SKU可查到，另外通过SKU查不到，但可通过“通用名”查到，CC-另外部分完全查询不到，DD-连通用名都无法查询到数据
  errorCode?: number; // 5200001 调用AI合理用药接口失败，请联系管理员
  onDrug?: (val: string) => void;
  onReload: () => void;
};

/**
 * 合理用药
 * @returns
 */
const RationalAdvice: React.FC<RationalAdviceProps> = (props: any) => {
  const {
    clas,
    currentStatus,
    interaction = [],
    interactionTip = null,
    avoid = [], // 使用禁忌
    avoidTip = null, // 药品紧急提示
    repeatMedication = [], // 重复用药
    repeatMedicationTip = null, // 重复用药提示
    beyondIndication = [], // 超适应症
    remarkCode,
    errorCode,
    onDrug,
    onReload,
  } = props;

  const [isExpandAll, setIsExpandAll] = useState(false);
  const [currentTab, setCurrentTab] = useState(0);
  const [currentMore, setCurrentMore] = useState('');

  useEffect(() => {
    setIsExpandAll(
      currentStatus && CLOSE_LIST[currentStatus] !== undefined ? CLOSE_LIST[currentStatus] : false,
    );
  }, [currentStatus]);

  function PopoverContent() {
    return (
      <div className={styles['popover-content']}>
        <h4 className={styles['popover-title']}>合理用药</h4>
        <div className={styles['popover-tip']}>
          基于药物知识图谱引擎，能够方便快捷地进行知识的推理泛化。
        </div>
        <div className={styles['popover-desc']}>
          注意：若没有发现两种药物之间存在相互作用或药物与其他条件存在警告信
          息，并不意味着没有，请在使用前与专业医生核对。
        </div>
      </div>
    );
  }

  function TooltipTit() {
    return (
      <div className={styles['tool-tip-tit']}>
        <div className={styles['tip-tit-item']}>轻度</div>
        <div className={styles['tip-tit-item']}>中度</div>
        <div className={styles['tip-tit-item']}>重度</div>
      </div>
    );
  }

  function YellowTip(tipData: any) {
    try {
      const copyTip = Object.keys(tipData).filter((el) => tipData[el]);
      return tipData?.commonNameAndCompanyMatch !== null ||
        tipData?.commonNameMatch !== null ||
        tipData?.notMatch !== null ||
        tipData?.skuMatch !== null ? (
        <div className={styles.tips}>
          <div>提示：</div>
          <ul>
            {copyTip.map((list, index) => {
              return tipData?.[list] ? (
                <li>
                  {index + 1}. {tipData?.[list]}
                </li>
              ) : null;
            })}
          </ul>
        </div>
      ) : null;
    } catch (err) {
      console.log(err);
      return null;
    }
  }

  return (
    <div
      className={`${styles['rational-advice']} ${clas} ${
        isExpandAll ? styles['rational-padding'] : ''
      }`}
    >
      <div className={styles['advice-wrapper']}>
        <h4 className={styles['advice-title']}>
          合理用药建议{' '}
          <Popover
            style={{ width: '478px' }}
            placement="right"
            content={PopoverContent()}
            trigger="hover"
          >
            {/* <QuestionCircleFilled style={{ cursor: 'pointer', color: 'rgba(0,0,0,0.25)' }} /> */}
            <InfoCircleFilled style={{ cursor: 'pointer', color: 'rgba(0,0,0,0.25)' }} />
          </Popover>
        </h4>
        <div className={styles['advice-tip']}>* 数据及技术由 MEDLEAP AI 开放平台 提供支持</div>
      </div>
      {/* tab */}
      <div className={`${styles.tab} ${isExpandAll ? styles['tab-margin'] : ''}`}>
        <ul className={styles['tab-container']}>
          {TAB_LIST?.map((el: any) => {
            return (
              <li
                className={`${styles['tab-item']} ${
                  currentTab === el?.value ? styles['tab-active'] : ''
                }`}
                key={el?.value}
                onClick={() => setCurrentTab(el.value)}
              >
                {el?.label} ({props?.[el?.flid]?.length})
              </li>
            );
          })}
        </ul>
        <div className={styles['expand-all']} onClick={() => setIsExpandAll(!isExpandAll)}>
          {!isExpandAll ? (
            <>
              收起全部 &nbsp;
              <CaretUpOutlined />
            </>
          ) : (
            <>
              展开全部 &nbsp;
              <CaretDownOutlined />
            </>
          )}
        </div>
      </div>
      {/* 内容区 */}
      {!isExpandAll ? (
        <ul className={styles['advice-container']}>
          {/* 相互作用 */}
          {currentTab === 0 && interaction.length > 0 ? (
            <>
              {YellowTip(interactionTip)}
              {interaction.map((item: any) => {
                return (
                  <li className={`${styles['advice-item']} ${styles['interaction-list']}`}>
                    <div className={styles['sub-tab']}>
                      <div className={styles['sub-item']}>
                        <span>{item?.ingredient_a?.ingredient}(成分)</span>
                        <span className={styles['sub-change']}>
                          <SwapOutlined
                            style={{ cursor: 'pointer', fontSize: 12, color: '#fff' }}
                          />
                        </span>
                        <span>{item?.ingredient_b?.ingredient}(成分)</span>
                      </div>
                      {/* <div className={styles['advice-severity']}>
                        <span className={styles.severity}>
                          <Tooltip overlayClassName="overlay-tool-tip" placement="top" title={TooltipTit()}>
                            <InfoCircleOutlined
                              style={{ cursor: 'pointer', color: 'rgba(0,0,0,0.25)', marginRight: '6px' }}
                            />
                          </Tooltip>
                          严重程度：
                        </span>
                        <span className={styles.status}>轻度</span>
                      </div> */}
                    </div>
                    <h4 className={styles['small-title']}>相互作用：</h4>
                    <div
                      className={`${styles.desc} ${
                        currentMore === item?.describe ? '' : styles['desc-ellipsis']
                      }`}
                    >
                      {item?.describe}
                    </div>
                    <span
                      className={styles['see-more']}
                      onClick={() =>
                        setCurrentMore(currentMore !== item?.describe ? item?.describe : '')
                      }
                    >
                      查看更多
                    </span>
                  </li>
                );
              })}
            </>
          ) : null}
          {currentTab === 0 && interaction.length === 0 ? (
            <>
              {YellowTip(interactionTip)}
              <li className={styles['no-data-wrapper']}>
                {/* {remarkCode === 'BB' || remarkCode === 'CC' ? (
              ) : null} */}
                {remarkCode === 'DD' ? (
                  <div className={styles['no-search-icon']} />
                ) : (
                  <>
                    <div className={styles['no-data-icon']} />
                    <span className={styles['no-data-title']}>本处方中的药品没有相互作用</span>
                  </>
                )}
              </li>
            </>
          ) : null}
          {currentTab === 0 && interaction.length === 0 && errorCode === 5200001 ? (
            <li className={styles['no-data-wrapper']}>
              <div className={styles['error-icon']} />
              <div>
                <span className={styles['no-data-title']}>接口异常</span>
                <Button type="link" onClick={() => onReload()}>
                  刷新
                </Button>
              </div>
            </li>
          ) : null}
          {/* 用药禁忌 */}
          {currentTab === 1 && avoid.length > 0 ? (
            <li className={styles['advice-item']}>
              {YellowTip(avoidTip)}
              {avoid?.map((item: any) => {
                return (
                  <div className={styles['avoid-list']}>
                    <div className={styles['drug-contrain']}>
                      <span
                        className={styles['contrain-title']}
                        onClick={() => onDrug && onDrug(item?.drugInfo?.drugid)}
                      >
                        {item?.drugInfo
                          ? `${item?.drugInfo?.common_name}（${item?.drugInfo?.company_name}）`
                          : null}
                      </span>
                      <span className={styles['contrain-tip']}>
                        <InfoCircleOutlined style={{ marginRight: 5 }} />
                        点击药品名称可溯源药品说明书
                      </span>
                    </div>
                    <div className={`${styles['title-wrapper']} ${styles.mb10}`}>
                      <span className={styles['taboo-crowd']}>禁忌人群：</span>
                      <span style={{ marginLeft: 6 }}>
                        {item?.avoid_info?.length
                          ? item?.avoid_info?.map((list: any) => {
                              return <span className={styles['taboo-labels']}>{list}</span>;
                            })
                          : null}
                      </span>
                    </div>
                    <div className={`${styles['taboo-crowd']} ${styles.mb6}`}>用药禁忌：</div>
                    <div
                      className={`${styles.desc} ${
                        currentMore === item?.drugInfo?.uid ? '' : styles['desc-ellipsis']
                      }`}
                    >
                      {item?.describe}
                    </div>
                    <span
                      className={styles['see-more']}
                      onClick={() =>
                        setCurrentMore(
                          currentMore !== item?.drugInfo?.uid ? item?.drugInfo?.uid : '',
                        )
                      }
                    >
                      查看更多
                    </span>
                  </div>
                );
              })}
            </li>
          ) : null}
          {currentTab === 1 && avoid.length === 0 ? (
            <>
              {YellowTip(avoidTip)}
              <li className={styles['no-data-wrapper']}>
                {/* {remarkCode === 'BB' || remarkCode === 'CC' ? (
              ) : null} */}
                {remarkCode === 'DD' ? (
                  <div className={styles['no-search-icon']} />
                ) : (
                  <>
                    <div className={styles['no-data-icon']} />
                    <span className={styles['no-data-title']}>本处方中的药品没有用药禁忌</span>
                  </>
                )}
              </li>
            </>
          ) : null}
          {currentTab === 1 && avoid.length === 0 && errorCode === 5200001 ? (
            <li className={styles['no-data-wrapper']}>
              <div className={styles['error-icon']} />
              <div>
                <span className={styles['no-data-title']}>接口异常</span>
                <Button type="link" onClick={() => onReload()}>
                  刷新
                </Button>
              </div>
            </li>
          ) : null}
          {/* 重复用药 */}
          {currentTab === 2 && repeatMedication.length > 0 ? (
            <li className={styles['advice-item']}>
              {YellowTip(repeatMedicationTip)}
              {repeatMedication.map((item: any) => {
                return (
                  <div className={styles['repeated-list']}>
                    <div className={styles['repeated-medication']}>
                      <div>
                        {item?.drugInfo?.map((list: any) => {
                          return (
                            <div
                              className={`${styles['contrain-title']} ${styles['repeated-title']} ${styles.mb6}`}
                              onClick={() => onDrug && onDrug(list?.drugid)}
                            >
                              {list?.common_name}(
                              {list?.re_company_name ? list?.re_company_name : list?.company_name})
                            </div>
                          );
                        })}
                      </div>
                      <div className={styles['contrain-tip']}>
                        <InfoCircleOutlined style={{ marginRight: 5 }} />
                        点击药品名称可溯源药品说明书
                      </div>
                    </div>
                    <div className={`${styles['title-wrapper']} ${styles.mb10}`}>
                      <span className={styles['repeated-small-title']}>重复成分：</span>
                      <span style={{ marginLeft: 6 }}>
                        <span className={styles['taboo-labels']}>{item?.ingredient}</span>
                      </span>
                    </div>
                  </div>
                );
              })}
            </li>
          ) : null}
          {currentTab === 2 && repeatMedication.length === 0 ? (
            <>
              {YellowTip(repeatMedicationTip)}
              <li className={styles['no-data-wrapper']}>
                {/* {remarkCode === 'BB' || remarkCode === 'CC' ? (
              ) : null} */}
                {remarkCode === 'DD' ? (
                  <div className={styles['no-search-icon']} />
                ) : (
                  <>
                    <div className={styles['no-data-icon']} />
                    <span className={styles['no-data-title']}>本处方中的药品没有重复用药</span>
                  </>
                )}
              </li>
            </>
          ) : null}
          {currentTab === 2 && repeatMedication.length === 0 && errorCode === 5200001 ? (
            <li className={styles['no-data-wrapper']}>
              <div className={styles['error-icon']} />
              <div>
                <span className={styles['no-data-title']}>接口异常</span>
                <Button type="link" onClick={() => onReload()}>
                  刷新
                </Button>
              </div>
            </li>
          ) : null}
          {/* 超适应症 */}
          {currentTab === 3 && beyondIndication.length > 0 ? (
            <div className={styles['advice-item']}>12</div>
          ) : null}
          {currentTab === 3 && beyondIndication.length === 0 ? (
            <li className={styles['no-data-wrapper']}>
              <div className={styles['no-data-icon']} />
              <span className={styles['no-data-title']}>暂无数据</span>
            </li>
          ) : null}
          {/* {currentTab === 3 && beyondIndication.length === 0 && errorCode === 5200001 ? (
            <li className={styles['no-data-wrapper']}>
              <div className={styles['error-icon']} />
              <div>
                <span className={styles['no-data-title']}>接口异常</span>
                <Button type="link" onClick={() => onReload()}>
                  刷新
                </Button>
              </div>
            </li>
          ) : null} */}
        </ul>
      ) : null}
    </div>
  );
};

export default RationalAdvice;
