/* eslint-disable indent */
import React, { useState } from 'react';
import { FileDoneOutlined } from '@ant-design/icons';
import { Badge, Table, Button, Space, Input, message } from '@sinohealth/butterfly-ui-antd/lib';
import styles from './index.less';

const Source: any = {
  manual: '手动录入',
  auto: '系统信息',
};

type DeliveryLogProps = {
  shippInfo?: AdminOrder.DeliveryInfo;
  receiptInfo?: AdminOrder.DeliveryInfo;
  status?: string;
  formType?: string; // orderSearch：订单查询 revieOrder：审方订单
  deliverySupplyer?: any;
  deliveryStatusDict?: any;
  deliverySource?: string;
  code?: string;
  entry?: (val: AdminOrder.EntryDelivery) => void;
  finsh?: () => void;
};

/**
 * 配送物流信息
 * @returns
 */
const DeliveryLog: React.FC<DeliveryLogProps> = (props: any) => {
  const {
    shippInfo,
    receiptInfo,
    status,
    deliveryStatusDict,
    code,
    formType,
    deliverySupplyer,
    deliverySource,
    entry,
    finsh,
  } = props;
  const [writeDeliver, setWriteDeliver] = useState(false);
  const [entryDelivery, setEntryDelivery] = useState({
    orderId: '',
    deliveryNo: '',
    deliveryCompany: '',
    courierName: '',
    courierPhone: '',
  });

  function titleDom(title: string) {
    return <div className={styles['shipp-info']}>{title}</div>;
  }

  const ShippInfoColumns = [
    {
      title: titleDom('配送信息'),
      dataIndex: 'orderId',
      key: 'orderId',
      width: 120,
      render: () => <div />,
    },
    {
      title: '信息来源',
      dataIndex: 'deliverySource',
      key: 'deliverySource',
      width: 100,
      render: (text: string | number) => <div>{Source[text] || '--'}</div>,
    },
    {
      title: '物流配送商',
      dataIndex: 'deliveryCode',
      key: 'deliveryCode',
      width: 120,
      render: (text: string | number) => <div>{deliverySupplyer && deliverySupplyer[text]}</div>,
    },
    {
      title: '运单编号',
      dataIndex: 'deliveryNo',
      key: 'deliveryNo',
      render: (text: string) =>
        writeDeliver ? (
          <Input
            placeholder="运单编号"
            value={entryDelivery.deliveryNo}
            style={{ width: '100px' }}
            onChange={(e: any) => {
              setEntryDelivery({
                ...entryDelivery,
                deliveryNo: e.target?.value,
              });
            }}
          />
        ) : (
          <span>{text || '--'}</span>
        ),
    },
    {
      title: '配送状态',
      key: 'deliveryStatus',
      dataIndex: 'deliveryStatus',
      width: 120,
      render: (text: string) => (
        <Badge color="#108ee9" text={deliveryStatusDict && deliveryStatusDict[text]} />
      ),
    },
    {
      title: () =>
        writeDeliver ? (
          <span>物流公司</span>
        ) : (
          <span>{code === 'akang' ? '物流公司' : '配送人员'}</span>
        ),
      key: 'deliveryUser',
      dataIndex: 'deliveryUser',
      width: 120,
      render: (text: string) =>
        writeDeliver ? (
          <Input
            placeholder="物流公司"
            value={entryDelivery.courierName}
            style={{ width: '100px' }}
            onChange={(e: any) => {
              setEntryDelivery({
                ...entryDelivery,
                courierName: e.target?.value,
              });
            }}
          />
        ) : (
          <span>{text || '--'}</span>
        ),
    },
    {
      title: '联系电话',
      key: 'deliveryPhone',
      dataIndex: 'deliveryPhone',
      width: 120,
      render: (text: string) => (
        // writeDeliver ? (
        //   <Input
        //     placeholder="联系电话"
        //     value={entryDelivery.courierPhone}
        //     style={{ width: '100px' }}
        //     onChange={(e: any) => {
        //       setEntryDelivery({
        //         ...entryDelivery,
        //         courierPhone: e.target?.value,
        //       });
        //     }}
        //   />
        // ) : (
        <span>{text || '--'}</span>
      ),
      // ),
    },
  ];

  const ReceiptInfoColumns = [
    {
      title: titleDom('收件信息'),
      dataIndex: 'id',
      key: 'id',
      width: 120,
      render: () => <div />,
    },
    { title: '收件人', dataIndex: 'toName', key: 'toName', width: 120 },
    { title: '收件地址', dataIndex: 'toAddress', key: 'toAddress' },
    { title: '联系电话', key: 'toPhone', dataIndex: 'toPhone', width: 120 },
  ];

  return (
    <div className={styles['delivery-log']}>
      <div className={styles['manual-entry']}>
        <h4 className={styles['log-title']}>配送物流信息</h4>
        {formType === 'revieOrder' ? (
          <Space>
            {!writeDeliver &&
            status === 'begin_delivery' &&
            deliverySource === null &&
            shippInfo.deliveryNo === null &&
            shippInfo.deliveryCode === 'akang' ? (
              <span className={styles['manual-title']} onClick={() => setWriteDeliver(true)}>
                <FileDoneOutlined style={{ marginRight: 4 }} />
                填写物流信息
              </span>
            ) : writeDeliver && status === 'begin_delivery' ? (
              <>
                <Button type="info" onClick={() => setWriteDeliver(false)}>
                  取消
                </Button>
                <Button
                  type="primary"
                  onClick={() => {
                    if (entryDelivery.deliveryNo === '') {
                      message.error('请填写配送单号');
                      return;
                    }
                    if (entryDelivery.courierName === '') {
                      message.error('请填写物流公司');
                      return;
                    }
                    entry && entry(entryDelivery);
                    const timer = setTimeout(() => {
                      setWriteDeliver(false);
                      clearTimeout(timer);
                    }, 1000);
                  }}
                >
                  保存
                </Button>
              </>
            ) : null}
            {deliverySource === 'manual' &&
            status === 'delivering' &&
            shippInfo.deliveryCode === 'akang' ? (
              <Button type="primary" icon={<FileDoneOutlined />} onClick={() => finsh && finsh()}>
                确认收货
              </Button>
            ) : null}
          </Space>
        ) : null}
      </div>
      {/* 配送信息 */}
      <Table rowKey="id" columns={ShippInfoColumns} dataSource={[shippInfo]} pagination={false} />
      {/* 收件信息 */}
      <Table
        rowKey="id"
        columns={ReceiptInfoColumns}
        dataSource={[receiptInfo]}
        pagination={false}
      />
    </div>
  );
};

export default DeliveryLog;
