import React, { useState } from 'react';
import { Spin, Tooltip, Modal, Button } from '@sinohealth/butterfly-ui-antd';
import {
  FullscreenOutlined,
  PrinterOutlined,
  VerticalAlignBottomOutlined,
  ZoomInOutlined,
  ZoomOutOutlined,
} from '@ant-design/icons';
import { Document, Page, pdfjs } from 'react-pdf';
import print from 'print-js';
import PdfPreview from '@/components/PdfPreview';
import styles from './index.less';
import { httpDownloadPrescription } from '@/services/system';
import { downloadXlsxOrCsv } from '@/utils/utils';

pdfjs.GlobalWorkerOptions.workerSrc = `//unpkg.com/pdfjs-dist@${pdfjs.version}/legacy/build/pdf.worker.min.js`;

type PrescriptPreviewProps = {
  file?: string;
  isCalc: boolean;
  createTime?: string;
  patientName?: string;
  diagnose?: string;
};

/**
 * 处方预览
 * @returns
 */
const PrescriptPreview: React.FC<PrescriptPreviewProps> = (props) => {
  const { file, isCalc, createTime, patientName, diagnose } = props;
  const [pageNumber, setPageNumber] = useState(1);
  const [percentage, setPercentage] = useState(100);
  const [loadSuccess, setLoadSuccess] = useState(false);
  const [pageWidth, setPageWidth] = useState(398);
  const [isModalVisible, setIsModalVisible] = useState(false);

  const onDocumentLoadSuccess = ({ numPages }: any) => {
    setPageNumber(numPages || 1);
    setLoadSuccess(true);
  };
  const pageZoomIn = () => {
    if (pageWidth < 800 && loadSuccess) {
      const newPageWidth = Math.trunc(pageWidth * 1.2);
      setPercentage(percentage + 20);
      setPageWidth(newPageWidth);
    }
  };
  const pageZoomOut = () => {
    if (pageWidth > 398 && loadSuccess) {
      const newPageWidth = Math.trunc(pageWidth * 0.8);
      setPercentage(percentage - 20);
      setPageWidth(newPageWidth);
    }
  };

  async function downLoadFile(url: string) {
    const res = await httpDownloadPrescription(`/api/${url.split('/api/')[1]}`);
    downloadXlsxOrCsv(res, 'pdf', `${patientName}-${diagnose}-${createTime}`);
  }

  // 打印
  function handlePrintPdf() {
    print({
      printable: file,
      type: 'pdf',
      scanStyles: false,
    });
  }

  return (
    <div className={styles['prescript-preview']}>
      <div className={styles.container}>
        <div className={styles['prescript-tools']}>
          <div>
            <Tooltip title="缩小">
              <span className={styles.enlarge} onClick={pageZoomOut}>
                <ZoomOutOutlined />
              </span>
            </Tooltip>
            <span className={styles['enlarge-number']}>{`${percentage}%`}</span>
            <Tooltip title="放大">
              <span className={styles.enlarge} onClick={pageZoomIn}>
                <ZoomInOutlined />
              </span>
            </Tooltip>
            <Tooltip title="全屏">
              <span className={`${styles['full-screen']}`}>
                <FullscreenOutlined onClick={() => setIsModalVisible(true)} />
              </span>
            </Tooltip>
          </div>
          <div className={styles.tool}>
            <Tooltip title="下载">
              <span className={styles.download} onClick={() => downLoadFile(file || '')}>
                <VerticalAlignBottomOutlined />
              </span>
            </Tooltip>
            <Tooltip title="打印">
              <span className={styles.print} onClick={() => handlePrintPdf()}>
                <PrinterOutlined />
              </span>
            </Tooltip>
          </div>
        </div>
        <div
          className={`${styles['prescript-container']} ${
            isCalc ? styles['prescript-container-calc'] : ''
          }`}
        >
          {file ? (
            <Document
              file={file}
              loading={<Spin size="small" />}
              renderMode="canvas"
              error="处方加载失败，请检查网络是否异常或处方异常"
              onLoadSuccess={onDocumentLoadSuccess}
              onLoadError={(err: any) => {
                console.log(err);
              }}
            >
              <Page pageNumber={pageNumber} width={pageWidth} loading={<Spin size="large" />} />
            </Document>
          ) : null}
        </div>
      </div>
      {/* <ul className={styles['prescript-change']}>
        <li className={`${styles['change-item']} ${styles['change-active']}`}>
          <span className={styles['item-page']}>1</span>
        </li>
        <li className={styles['change-item']}>
          <span className={styles['item-page']}>2</span>
        </li>
        <li className={styles['change-item']}>
          <span className={styles['item-page']}>3</span>
        </li>
        <li className={styles['change-item']}>
          <span className={styles['item-page']}>4</span>
        </li>
      </ul> */}
      <PdfPreview
        pageNumber={pageNumber}
        visible={isModalVisible}
        file={file}
        cancel={() => setIsModalVisible(false)}
      />
    </div>
  );
};

export default PrescriptPreview;
