/* eslint-disable no-nested-ternary */
import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Badge, Input, Select } from '@sinohealth/butterfly-ui-antd';
import { ClockCircleOutlined, SearchOutlined } from '@ant-design/icons';
import { ComputedColor } from '../../useDetailHook';

import styles from './index.less';

const { Option } = Select;

type SideOrderProps = {
  list: AdminOrder.GetListRes[];
  loading: boolean;
  total?: number;
  height?: string;
  id: string;
  formType?: string; // orderSearch：订单查询 revieOrder：审方订单
  tab?: string;
  delivDict?: any;
  onScroll: () => void;
  onTab?: (val: string) => void;
  onChange?: (id: string) => void;
  onSearch?: (keyword: string) => void;
};

const SideOrder: React.FC<SideOrderProps> = (props) => {
  const {
    list,
    loading,
    id,
    total = 0,
    height,
    formType,
    delivDict,
    onTab,
    tab,
    onScroll,
    onChange,
    onSearch,
  } = props;
  // eslint-disable-next-line no-bitwise
  const [dom, setDom] = useState<any>(null);
  const [searchWord, setSearchWord] = useState('');

  const navigate = useNavigate();

  const handleOnScroll = () => {
    if (dom) {
      const contentScrollTop = dom?.scrollTop; // 滚动条距离顶部
      const clientHeight = dom?.clientHeight; // 可视区域
      const scrollHeight = dom?.scrollHeight; // 滚动条内容的总高度
      if (contentScrollTop + clientHeight >= scrollHeight) {
        if (total !== list.length) onScroll();
      }
    }
  };

  return (
    <div className={styles['side-order']}>
      <div className={styles['side-search']}>
        {formType === 'orderSearch' ? null : (
          <Select
            defaultValue={tab}
            style={{ width: '100%', marginBottom: '12px' }}
            onChange={(value) => {
              navigate(
                `/prescrip/order/${
                  formType === 'orderSearch' ? 'detail' : 'reviewDetail'
                }?id=${id}&type=${value}`,
              );
              onTab && onTab(value);
            }}
          >
            <Option value="wait">待接单</Option>
            <Option value="wait_delivery">待配送</Option>
            <Option value="delivering">配送中</Option>
            <Option value="exception">异常</Option>
            <Option value="completed">已完成</Option>
            <Option value="expired">已失效</Option>
          </Select>
        )}
        <Input
          className={styles['search-input']}
          placeholder="处方号/订单号/手机号"
          bordered={false}
          value={searchWord}
          // suffix={<SearchOutlined onClick={() => onSearch && onSearch(searchWord)} />}
          suffix={
            <span
              className={styles['search-icon']}
              onClick={() => onSearch && onSearch(searchWord)}
            />
          }
          onChange={(e) => setSearchWord(e.target.value)}
          onPressEnter={() => onSearch && onSearch(searchWord)}
        />
      </div>
      <ul
        className={styles['side-container']}
        style={{ height }}
        ref={(d: any) => {
          setDom(d);
        }}
        onScrollCapture={() => handleOnScroll()}
      >
        {list.map((el) => (
          // eslint-disable-next-line jsx-a11y/no-noninteractive-element-interactions
          <li
            onClick={() => {
              // navigate(
              //   `/prescrip/order/${formType === 'orderSearch' ? 'detail' : 'reviewDetail'}?id=${
              //     el.id
              //   }${formType === 'orderSearch' ? '' : `&type=${tab}`}`,
              // );
              if (el.id !== id) {
                onChange && onChange(el.id);
              }
            }}
            className={`${styles['side-item']} ${id === el.id ? styles['item-active'] : ''}`}
            key={el.id}
          >
            <div className={styles['item-user']}>
              <div className={styles['user-info']}>
                <span className={`${styles['user-avator']} ${styles[el.deliveryCategory]}`}>
                  {el?.deliveryCategory}
                </span>
                <span className={styles['user-name']}>{el?.patientName}</span>
              </div>
              {/* <div className={styles.status}>审方中…</div> */}
              {el?.timeout && el?.timeout >= 0 ? (
                el?.deliveryCategory === 'O2O' ? (
                  <Badge
                    className={styles.status}
                    color={ComputedColor(el?.timeout, el?.deliveryCategory)?.color}
                    size="small"
                    text={el?.timeout > 5 ? '超时订单' : '新处方'}
                  />
                ) : (
                  <Badge
                    className={styles.status}
                    color={ComputedColor(el?.timeout, el?.deliveryCategory)?.color}
                    size="small"
                    text={el?.timeout > 12 ? '超时订单' : '新处方'}
                  />
                )
              ) : null}
            </div>
            <div className={styles['item-price']}>
              <span className={styles.price}>处方金额：¥{el?.prescriptionAmount}</span>
              {/* <span className={styles.type}>{el?.deliveryCategory}</span> */}
            </div>
            <div className={styles.time}>
              订单时间：{el?.createTime?.replace(/-/g, '.')?.slice(0, -3)}
            </div>
            {formType === 'revieOrder' && el?.timeout ? (
              el?.deliveryCategory === 'O2O' ? (
                el?.timeout > 5 ? (
                  <div
                    className={styles.timeout}
                    style={{ color: ComputedColor(el?.timeout, el?.deliveryCategory)?.color }}
                  >
                    <ClockCircleOutlined style={{ marginRight: 5 }} />
                    {`超时 ${el?.timeout} ${el?.deliveryCategory === 'O2O' ? '分钟' : '小时'}`}
                  </div>
                ) : (
                  <div className={`${styles.timeout} ${styles.news}`}>新处方</div>
                )
              ) : el?.timeout > 12 ? (
                <div
                  className={styles.timeout}
                  style={{ color: ComputedColor(el?.timeout, el?.deliveryCategory)?.color }}
                >
                  <ClockCircleOutlined style={{ marginRight: 5 }} />
                  {`超时 ${el?.timeout} ${el?.deliveryCategory === 'O2O' ? '分钟' : '小时'}`}
                </div>
              ) : (
                <div className={`${styles.timeout} ${styles.news}`}>新处方</div>
              )
            ) : null}
          </li>
        ))}
        {total ? (
          <li className={styles['load-more']}>{loading ? '正在加载...' : '· 没有更多了 ·'}</li>
        ) : (
          <li className={styles['load-data']} />
        )}
      </ul>
    </div>
  );
};

export default SideOrder;
