import { CaretDownOutlined, CaretUpOutlined } from '@ant-design/icons';
import { Button, Table, Tooltip } from '@sinohealth/butterfly-ui-antd/lib';
import React, { useEffect, useState } from 'react';
import styles from './index.less';

const CLOSE_LIST: any = {
  delivering: true,
  delivery_exception: true,
  finished: true,
};

type DrugListProps = {
  clas: any;
  drugList?: AdminOrder.DrugList[];
  totalAmount?: number;
  deliveryAmount?: number;
  currentStatus?: string;
  drugFrequency?: any;
  drugUsage?: any;
};

/**
 * 处方审核药品清单
 * @returns
 */
const DrugList: React.FC<DrugListProps> = (props) => {
  const {
    clas,
    drugList,
    drugFrequency,
    drugUsage,
    totalAmount = 0,
    deliveryAmount = 0,
    currentStatus,
  } = props;
  const [isClose, setIsClose] = useState(false);

  useEffect(() => {
    setIsClose(
      currentStatus && CLOSE_LIST[currentStatus] !== undefined ? CLOSE_LIST[currentStatus] : false,
    );
  }, [currentStatus]);

  const total = (totalAmount - deliveryAmount).toFixed(2);

  function titleDom() {
    return (
      <Button
        type="text"
        size="small"
        onClick={() => {
          setIsClose(true);
        }}
      >
        收起
        <CaretUpOutlined />
      </Button>
    );
  }

  const columns: any = [
    { title: titleDom(), dataIndex: 'index', key: 'index', width: 60 },
    {
      title: '药品名称',
      dataIndex: 'name',
      key: 'name',
      render: (_: any, record: any) => (
        <Tooltip title={_}>
          <div className={styles.ellipsis}>{_}</div>
        </Tooltip>
      ),
    },
    {
      title: '药品规格',
      dataIndex: 'spec',
      key: 'spec',
      width: 100,
      render: (_: any, record: any) => (
        <Tooltip title={_}>
          <span className={styles.ellipsis}>{_}</span>
        </Tooltip>
      ),
    },
    {
      title: '用法',
      key: 'usage',
      dataIndex: 'usage',
      width: 70,
      render: (_: any, record: any) => (
        <Tooltip title={drugUsage && drugUsage[record.usage]}>
          <span className={styles.ellipsis}>{drugUsage && drugUsage[record.usage]}</span>
        </Tooltip>
      ),
    },
    {
      title: '用量',
      dataIndex: 'dosage',
      key: 'dosage',
      width: 170,
      render: (_: any, record: any) => (
        <Tooltip
          title={`${drugFrequency && drugFrequency[record.frequency]}， 每次
        ${record.singleDosage}`}
        >
          <span className={styles.ellipsis}>
            {drugFrequency && drugFrequency[record.frequency]}， 每次
            {record.singleDosage}
          </span>
        </Tooltip>
      ),
    },
    { title: '数量', dataIndex: 'quantity', key: 'quantity', width: 60 },
    { title: '单价', dataIndex: 'salePrice', key: 'salePrice', width: 60 },
    {
      title: () => (
        <Tooltip
          placement="bottomRight"
          overlayClassName="price-wrapper"
          title={() => <div className={styles['price-tip']}>金额 = 单价 * 数量</div>}
        >
          <div className={styles['price-title']}>金额</div>
        </Tooltip>
      ),
      align: 'right',
      dataIndex: 'salePrice',
      key: 'salePrice',
      width: 70,
      render: (_: any, record: any) => (
        <span>￥{(record.salePrice * record.quantity).toFixed(2)}</span>
      ),
    },
  ];

  return (
    <div className={`${styles['drug-container']} ${clas} ${styles[currentStatus || '']}`}>
      <div className={styles['drug-wrapper']}>
        <h4 className={styles['drug-title']}>处方审核药品清单</h4>
        <span className={styles['drug-price']}>处方金额：¥{total}</span>
      </div>
      {isClose ? (
        <>
          {/* 关闭 */}
          <div className={styles['drug-close']}>
            <div className={styles['close-title']} onClick={() => setIsClose(false)}>
              处方明细 ({drugList?.length}) &nbsp;
              <CaretDownOutlined />
            </div>
            <div className={styles['close-info']}>
              <span className={styles['close-drug']}>{drugList?.map((el) => `${el.name};`)}</span>等{' '}
              {drugList?.length} 项药品
            </div>
          </div>
        </>
      ) : (
        <Table
          rowKey="id"
          pagination={false}
          bordered={false}
          columns={columns}
          dataSource={drugList}
        />
      )}
    </div>
  );
};

export default DrugList;
