import React from 'react';
import { Breadcrumb } from '@sinohealth/butterfly-ui-antd/lib';
import { useNavigate } from 'react-router-dom';

import styles from './index.less';

type BreadcrumbObj = {
  label: string;
  link?: string;
};

type BackBreadcrumbProps = {
  breadcrumbMap: BreadcrumbObj[];
  link: string;
};

/**
 * 面包屑
 * @returns
 */
const BackBreadcrumb: React.FC<BackBreadcrumbProps> = (props) => {
  const { breadcrumbMap, link } = props;

  const navigate = useNavigate();

  return (
    <div className={styles['breadcrumb-container']}>
      <div
        className={`${styles['breadcrumb-back']} ${styles.cursor}`}
        onClick={() => {
          navigate(link);
        }}
      >
        <span className={styles['back-icon']} />
        返回
      </div>
      {breadcrumbMap.length ? (
        <Breadcrumb>
          {breadcrumbMap?.map((el, index) => {
            return (
              <Breadcrumb.Item
                key={el.label}
                onClick={() => el.link && navigate(el.link)}
                className={`${
                  index === breadcrumbMap.length - 1 ? styles['breadcrumb-last'] : ''
                } ${el.link ? styles.cursor : ''}`}
              >
                {el.label}
              </Breadcrumb.Item>
            );
          })}
        </Breadcrumb>
      ) : null}
    </div>
  );
};

export default BackBreadcrumb;
