import { useEffect, useState } from 'react';
import moment from 'moment';
import { httpQueryRationalDrugByOrderId } from '@/services/system';

/**
 * 订单列表倒计时
 * @param time 订单时间
 * @param type 订单类型
 * @returns
 */
export const useTimeoutTip = (time: string, type: string) => {
  const TYPES: any = {
    O2O: 'minutes',
    B2C: 'hours',
  };
  const computeTime = moment(Date.now()).diff(time, TYPES[type]);
  return `${computeTime}`;
};

export const useRationalDrug = (orderId: string, errorMsg?: boolean) => {
  const [rationalDrug, setRationalDrug] = useState<AdminOrder.RationalDrug>({});
  const [errorCode, setErrorCode] = useState(0);

  async function httpQueryRationalDrugByOrderIdReq() {
    try {
      const res = await httpQueryRationalDrugByOrderId(orderId);
      console.log(res);
      setRationalDrug(res);
      setErrorCode(200);
    } catch (err: any) {
      setErrorCode(err?.code);
    }
  }

  useEffect(() => {
    httpQueryRationalDrugByOrderIdReq();
  }, [orderId, errorMsg]);

  return { ...rationalDrug, errorCode };
};

export const ComputedColor = (time?: any, type?: string): any => {
  if (type === 'B2C') {
    if (time < 12) {
      return {
        color: '#217BA0',
        backgroundImage: 'linear-gradient(180deg, #67B7E2 0%, #217BA0 100%)',
      };
    }
    if (time < 36) {
      return {
        color: '#FFC100',
        backgroundImage: 'linear-gradient(180deg, #FFE78A 0%, #FFC464 100%)',
      };
    }
    if (time < 60) {
      return {
        color: '#FF6100',
        backgroundImage: 'linear-gradient(180deg, #FFCE8A 0%, #FF8147 100%)',
      };
    }
    return {
      color: '#F53F3F',
      backgroundImage: 'linear-gradient(180deg, #FF8585 0%, #FF5656 100%)',
    };
  }
  if (time < 5) {
    return {
      color: '#217BA0',
      backgroundImage: 'linear-gradient(180deg, #67B7E2 0%, #217BA0 100%)',
    };
  }
  if (time < 10) {
    return {
      color: '#FFC100',
      backgroundImage: 'linear-gradient(180deg, #FFE78A 0%, #FFC464 100%)',
    };
  }
  if (time < 15) {
    return {
      color: '#FFC100',
      backgroundImage: 'linear-gradient(180deg, #FFE78A 0%, #FFC464 100%)',
    };
  }
  return {
    color: '#FFC100',
    backgroundImage: 'linear-gradient(180deg, #FFE78A 0%, #FFC464 100%)',
  };
};

export default {
  useTimeoutTip,
};
