/* eslint-disable no-nested-ternary */
import React from 'react';
import { Tooltip } from '@sinohealth/butterfly-ui-antd/lib';
import { ClockCircleOutlined } from '@ant-design/icons';
import styles from './index.less';
import { ComputedColor } from '../../useDetailHook';

// 流程状态
// wait-待接单
// received-已接单
// dispense_success-已审方配药
// delivering-物流配送中,
// delivery_exception:配送异常,
// signed-确认收货

// 订单详情当前状态
// wait：待接单,
// received：已接单,
// rejected：已拒单,
// timeout：已超时,
// begin_delivery：发起配送,
// delivering：配送中,
// delivery_exception:配送异常,
// finished：配送完成
type OrderStatusProps = {
  clas: any;
  id?: string;
  tip?: any;
  processStatus?: string;
  receiveTime?: string;
  doctor?: string;
  pCreateTime?: string;
  dispensePharmacist?: string;
  createTime?: string;
  auditPharmacist?: string;
  deliveryUser?: string;
  signTime?: string;
  sourceTime?: string;
  signUser?: string;
  currentStatus?: AdminOrder.CurrentStatus;
  message?: string;
  invalidReason?: string;
  deliveryCategory?: any;
  formType?: string; // orderSearch：订单查询 revieOrder：审方订单
  currentStatusDict?: any; // 字典
};

const STATUS_ENU: any = {
  wait: 0, // 提交处方
  received: 1, // 提交处方
  timeout: 1, // 提交处方
  rejected: 2, // 审方配药
  dispense_success: 3, // 审方配药
  delivering: 4, // 物流配送
  invalid: 5, // 物流配送
  delivery_exception: 6, // 物流配送
  signed: 7, // 确认收货
};

// eslint-disable-next-line no-shadow
enum CURRENT_STATUS {
  wait = '待接单',
  received = '已接单',
  rejected = '已拒单',
  timeout = '已超时',
  begin_delivery = '发起配送',
  delivery_wait = '待配送',
  delivering = '配送中',
  delivery_exception = '配送异常',
  finished = '配送完成',
}

const TYPES: any = {
  O2O: '分钟',
  B2C: '小时',
};

/**
 * 订单审方状态流转
 * @returns
 */
const OrderStatus: React.FC<OrderStatusProps> = (props) => {
  const {
    clas,
    id,
    tip,
    processStatus,
    receiveTime,
    doctor,
    pCreateTime,
    dispensePharmacist,
    createTime,
    sourceTime,
    auditPharmacist,
    deliveryUser,
    signTime,
    signUser,
    currentStatus,
    message,
    invalidReason,
    deliveryCategory,
    formType,
    currentStatusDict,
  } = props;

  const currentStep = STATUS_ENU[processStatus || 'wait'];

  return (
    <div className={`${styles['order-status-container']} ${clas}`}>
      <div className={styles['status-tool']}>
        <div className={styles['status-info']}>
          <span className={`${styles['status-title']}`}>订单编号：</span>
          <span className={styles['status-no']}>{id}</span>
          {/* <span className={styles['status-time']}>订单时间：{sourceTime || '--'}</span> */}
        </div>
        <div
          className={`${styles['order-status']} ${currentStatus && styles[currentStatus]}
          `}
          style={{ backgroundImage: ComputedColor(tip, deliveryCategory)?.backgroundImage }}
        >
          {currentStatus === 'wait' && formType === 'revieOrder' && tip > 12 ? (
            <ClockCircleOutlined style={{ marginRight: 5 }} />
          ) : null}
          {currentStatus === 'wait' && formType === 'revieOrder' ? (
            <span>
              {tip > 12
                ? `超时 ${tip} ${deliveryCategory && TYPES[deliveryCategory]} | `
                : '新处方 | '}
            </span>
          ) : null}
          {currentStatus ? currentStatusDict[currentStatus] : ''}
        </div>
      </div>
      <ul className={styles['step-container']}>
        {/* 提交处方 */}
        <li
          className={`${styles['step-item']} ${currentStep >= 1 ? styles['step-active'] : ''} ${
            currentStep >= 2 ? styles['step-finsh'] : ''
          }`}
        >
          <span className={styles['item-icon']} />
          <div className={styles['step-title']}>提交处方</div>
          {currentStep >= 1 || currentStep >= 2 ? (
            <>
              <div className={styles['step-time']}>{pCreateTime || '--'}</div>
              <div className={styles['step-name']}>
                医师：{doctor || '--'} 药师：{auditPharmacist || '--'}
              </div>
            </>
          ) : null}
        </li>
        {/* 审方配药 */}
        <li
          className={`${styles['step-item']} ${
            currentStep >= 2 || currentStep >= 3 ? styles['step-active'] : ''
          } ${currentStep >= 4 ? styles['step-finsh'] : ''} ${
            currentStep === 4 ? styles['step-error'] : ''
          }`}
        >
          <span className={styles['item-icon']} />
          <div className={styles['step-title']}>审方配药</div>
          {currentStep === 2 ? (
            <>
              <div className={styles['step-time']}>{sourceTime || '--'}</div>
              <Tooltip
                title={
                  `${invalidReason} 药师：${auditPharmacist || '--'}` ||
                  `驳回原因：${message} 药师：${auditPharmacist || '--'}`
                }
              >
                <div className={`${styles['step-name']} ${styles['error-name']}`}>
                  {invalidReason || `驳回原因：${message}`} 药师：{auditPharmacist || '--'}
                </div>
              </Tooltip>
            </>
          ) : null}
          {currentStep >= 3 ? (
            <>
              <div className={styles['step-time']}>{receiveTime || '--'}</div>
              {dispensePharmacist ? (
                <div className={styles['step-name']}>发药师：{dispensePharmacist || '--'}</div>
              ) : null}
            </>
          ) : null}
        </li>
        {/* 物流配送 */}
        <li
          className={`${styles['step-item']} ${
            currentStep >= 4 || currentStep >= 5 || currentStep >= 6 ? styles['step-active'] : ''
          } ${currentStep >= 7 ? styles['step-finsh'] : ''} ${
            processStatus === 'delivery_exception' ? styles['step-error'] : ''
          }`}
        >
          <span className={styles['item-icon']} />
          <div className={styles['step-title']}>
            {deliveryCategory ? <span>【{deliveryCategory}】</span> : null}物流配送
          </div>

          {currentStep >= 4 || currentStep >= 5 || currentStep >= 6 ? (
            <>
              {createTime ? <div className={styles['step-time']}>{createTime}</div> : null}

              {currentStep >= 5 ? (
                <Tooltip title={invalidReason}>
                  <div className={`${styles['step-name']} ${styles['error-name']}`}>
                    {invalidReason}
                  </div>
                </Tooltip>
              ) : null}
              {invalidReason ? null : (
                <div className={styles['step-name']}>配送人员：{deliveryUser || '--'}</div>
              )}
            </>
          ) : null}
        </li>
        {/* 确认收货 */}
        <li className={`${styles['step-item']} ${currentStep >= 7 ? styles['step-active'] : ''}`}>
          <span className={styles['item-icon']} />
          <div className={styles['step-title']}>确认收货</div>
          {currentStep >= 7 ? (
            <>
              {signTime ? <div className={styles['step-time']}>{signTime || '--'}</div> : null}
              {signUser ? (
                <div className={styles['step-name']}>收货员：{signUser || '--'}</div>
              ) : null}
            </>
          ) : null}
        </li>
      </ul>
    </div>
  );
};

export default OrderStatus;
